@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;600;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.about-box{
    background-image: linear-gradient(to right, #181717 0%, black 100%);
}

/* Mobile View */
@media screen and (max-width: 768px){
    .about-data{
        display: grid;
    }   
    .about-data p{
        margin-left: 16px;
        margin-right: 16px;
        font-size: 13px;
        font-family:'Poppins', sans-serif ;
        text-align: justify;
        padding: 15px;
        color: rgb(220,225,219);
    }
}

/* Desktop View */
.about-title{
    position: relative;
    color: white;
    text-align: center;
    font-size: 32px;
    padding: 25px;
}

.about-img img{
    width: 100%;
    max-width: 700px;
    border-radius: 25px;
    padding: 15px;
}

@media screen and (min-width:768px) {
    .about-data p{
        margin-left: 50px;
        margin-right: 50px;
        font-size: 15px;
        font-family:'Poppins', sans-serif ;
        text-align: justify;
        color: rgb(220,225,219);
        padding: 15px;
    }
    .about-data{
        display: grid;
        grid-template-columns: 50% 50%;
    } 
}

.footer {
  background-image: linear-gradient(to right, #181717 0%, black 100%);
  color: #ffffff;
  padding: 40px 20px;
  font-family: Arial, sans-serif;
}

.footer-data {
  display: flex;
  justify-content: space-around; /* Distributes equal space around each section */
  grid-gap: 15px;
  gap: 15px;
  border-bottom: 1px solid rgba(255, 255, 255, 0.2);
  padding-bottom: 20px;
  margin-bottom: 20px;
}



.footer h1 {
  font-size: 2rem;
  margin-bottom: 15px;
  color: #ffffff;
}

.footer p, .footer a {
  font-size: 1.1rem;
  color: #d0d0d0;
  line-height: 1.7;
  margin: 6px 0;
}

.useful-links a {
  display: block;
  color: #d0d0d0;
  text-decoration: none;
  transition: color 0.3s;
}

.useful-links a:hover {
  color: #ce5d63;
}

.contact-social {
  display: flex;
  grid-gap: 15px;
  gap: 15px;
  justify-content: center;
  margin-top: 10px;
}

.social-icon {
  color: #ffffff;
  font-size: 1.9rem;
  transition: transform 0.3s, color 0.3s;
}

.social-icon:hover {
  color: #ce5d63;
  transform: scale(1.1);
}

.copyright-details {
  text-align: center;
  font-size: 1.1rem;
  color: #d0d0d0;
}

copyright-details span {
  display: block;
  color: #ce5d63;
  margin-top: 5px;
}



img{
    width: auto;
    height: 500px;
}

/* Mobile View */
@media screen and (max-width:768px){
    img{
        height: 250px;
    }
}
.slider {
    position: relative;
}

.welcome-message {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    text-align: center;
    color: white;  /* Adjust the color for readability */
