.welcome-message h2 {
    font-size: 2rem;  /* Increase the font size */
    margin-bottom: 20px;
}

.welcome-message p {
    font-size: 1.2rem;  /* Increase the font size */
    line-height: 1.6;   /* Increase the line height for better readability */
    margin-bottom: 20px;
}

