.news-container {
    background-image: linear-gradient(to right, #181717 0%, black 100%);
    padding: 20px;
    color: white;
}

.news-title {
    text-align: center;
    font-size: 2em;
    margin-bottom: 20px;
}

.news-subtitle {
    font-size: 1.5em;
    margin: 20px 0;
    text-align: center;
}

.news-data {
    display: flex;
    justify-content: space-between;
}

.news-empty {
    flex: 1 1; /* Flex space for better layout */
}

.news-competition-event,
.news-quiz-event {
    background-color: #1a2a3a; /* Slightly lighter background for events */
    margin: 10px 0;
    padding: 15px;
    border-radius: 5px;
    transition: transform 0.3s, box-shadow 0.3s;
}

.news-competition-event:hover,
.news-quiz-event:hover {
    transform: scale(1.05); /* Scale on hover */
    box-shadow: 0 4px 20px rgba(255, 255, 255, 0.2); /* Light white shadow on hover */
    background-color: #2a3b4a; /* Slightly change background on hover for better visibility */
}

.fade-in {
    animation: fadeIn 0.5s forwards;
}

@keyframes fadeIn {
    from {
        opacity: 0;
        transform: translateY(20px);
    }
    to {
        opacity: 1;
        transform: translateY(0);
    }
}

.photogallery-container {
  width: 100%;
  height: auto;
  background-image: linear-gradient(to right, #181717 0%, black 100%);
  padding: 15px;
  display: flex;
  flex-direction: column;
}

.photogallery-title {
  padding-top: 15px;
  text-align: center;
  color: #fcf1ce;
}

.photogallery-images {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  grid-gap: 20px;
  gap: 20px;
  padding: 15px;
}

.photogallery-images img {
  width: 100%;
  max-width: 400px;
  max-height: 300px;
  display: block;
  border-radius: 5px;
  transition: all 100ms ease-out;
}

.photogallery-images img:hover {
  transform: scale(1.04);
  transition: all ease 0.5s;
}

.photogallery-container button {
  font-size: 18px;
  font-weight: 800;
  padding: 8px 5px;
  border-radius: 5px;
  outline: none;
  align-items: left;
  margin-left: 12px;
  width: 160px;
  color: white;
  border: 1px solid white;
  background-color: rgb(233, 83, 75);
}

.photogallery-container button:hover {
  background-color: white;
  color: rgb(233, 83, 75);
  border: 1px solid rgb(233, 83, 75);
  transition: all ease 0.5s;
}

.popularbooks-container{
    background-image: linear-gradient(to right, #181717 0%, black 100%);
    display: flex;
    flex-direction: column;
}

.popularbooks{
    padding: 20px;
    overflow: hidden;
    width: 100%;
    margin: 0 auto;	
    position: relative;
	display: flex;
    height: auto;
}

.popularbooks:hover div{
    animation-play-state: paused;
}

.popularbooks-title{
    position: relative;
    color: white;
    margin-top: 20px;
    margin-bottom: 50px;
    text-align: center;
    font-size: 32px;
}

.popular-book:hover{
    transform: scale(1.15);
    transition: all ease 0.7s;
    animation-play-state: paused;
}

.popularbook-images{
    animation: marquee 15s linear infinite;
    display: flex;
    position: relative;
    align-items: center;
    justify-content: center;
}

.popular-book{
    display: block;
    width: 150px;
    height: auto;
    margin: 0 1vw;
    border-radius: 5px;
}

@keyframes marquee {
	0%   { transform: translateX(0%); }
	100% { transform: translateX(-100%); }
}
.recentaddedbooks-container{
    background-image: linear-gradient(to right, #181717 0%, black 100%);
    display: flex;
    flex-direction: column;
}

.recentbooks{
    padding: 20px;
    overflow: hidden;
    width: 100%;
    margin: 0 auto;	
    position: relative;
	display: flex;
}

.recentbooks:hover div{
    animation-play-state: paused;
}

.recentbooks-title{
    position: relative;
    color: rgb(255, 255, 255);
    font-size: 32px;
    text-align: center;
}

.recent-book:hover{
    transform: scale(1.15);
    transition: all ease 0.7s;
    animation-play-state: paused;
}

.images{
    animation: marquee 15s linear infinite;
    display: flex;
    position: relative;
    align-items: center;
    justify-content: center;
}

.recent-book{
    display: block;
    width: 150px;
    height: auto;
    margin: 0 1vw;
    border-radius: 5px;
}

@keyframes marquee {
	0%   { transform: translateX(0%); }
	100% { transform: translateX(-100%); }
}
