/* ReservedBooks.css */

/* ReservedBooks.css */

.reservedbooks-container {
    max-width: 100%;
    margin: 30px auto;
    padding: 30px;
    background-image: linear-gradient(to right, #181717 0%, black 100%);
    /* border-radius: 8px; */
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
    transition: transform 0.3s ease-in-out;
}

.reservedbooks-container:hover {
    transform: scale(1.02);
}

.reservedbooks-title {
    font-size: 2.5em;
    color: #ffffff; /* White for contrast */
    text-align: center; /* Center alignment */
    margin-bottom: 25px;
    align-items: center;
    display: flex;
    justify-content: center;
    text-transform: uppercase;
    font-weight: bold;
}

.reservedbooks-table {
    width: 100%;
    border-collapse: collapse;
    margin-top: 20px;
}
td a{
    text-decoration: none;
    color: #000;
}

.reservedbooks-table th, .reservedbooks-table td {
    background-color: #333333;
    padding: 15px;
    text-align: left;
    font-size: 1em;
}

.reservedbooks-table th {
    background-color: #0d6efd; /* Professional blue for header */
    color: #ffffff;
    font-weight: bold;
    text-transform: uppercase;
    font-size: 1.1em;
    border: 1px solid #dee2e6;
}

.reservedbooks-table td {
    border: 1px solid #dee2e6;
    background-color: #f8f9fa;
    color: #333333; /* Dark text for readability */
    transition: background-color 0.3s ease-in-out, color 0.3s ease-in-out;
}

.reservedbooks-table tr:hover td {
    background-color: #0d6efd;
    color: #ffffff;
}

.reservedbooks-table tr:nth-child(even) td {
    background-color: #e9ecef;
}

.reservedbooks-table tr {
    transition: transform 0.2s ease-in-out;
}

.reservedbooks-table tr:hover {
    transform: translateX(5px);
}

/* Stats Section Styling */
.stats {
    background-image: linear-gradient(to right, #181717 0%, black 100%);
    padding: 40px 20px;
    border-radius: 10px;
    box-shadow: 0 8px 16px rgba(0, 0, 0, 0.1);
    text-align: center;
    max-width: 100%;
    margin: 0 auto;
  }
  
  .stats h1 {
    font-size: 2rem;
    color: #ffffff;
    margin-bottom: 30px;
    font-weight: bold;
  }
  
  .stats-block {
    background-color: #eeeaea;
    border-radius: 10px;
    padding: 20px;
    box-shadow: 0 6px 12px rgba(0, 0, 0, 0.1);
    margin: 15px;
    text-align: center;
    flex: 1 1;
    transition: transform 0.3s ease, box-shadow 0.3s ease;
  }
  
  .stats-block:hover {
    transform: translateY(-10px);
    box-shadow: 0 12px 24px rgba(0, 0, 0, 0.15);
  }
  
  .stats-icon {
    color: #ce5d63;
    margin-bottom: 10px;
  }
  
  .stats-title {
    font-size: 1.2rem;
    color: #555;
    font-weight: 500;
    margin: 10px 0 5px;
  }
  
  .stats-count {
    font-size: 2rem;
    color: #333;
    font-weight: bold;
  }
  
  .stats-container {
    display: flex;
    grid-gap: 15px;
    gap: 15px;
    justify-content: space-around;
    flex-wrap: wrap;
    padding: 20px 0;
  }
  
.signin-container {
  display: grid;
  place-items: center;
  height: 100vh;
  background-color: wheat;
  position: relative;
}

a {
  text-decoration: none;
}

.signin-card {
  font-family: sans-serif;
  width: 100%;
  max-width: 350px;
  margin-left: auto;
  margin-right: auto;
  margin-top: 38px;
  margin-bottom: 38px;
  border-radius: 10px !important;
  background-color: #ffff;
  box-shadow: 2px 5px 20px rgba(0, 0, 0, 0.1);
}

form {
  padding: 30px;
}

.error-message{
  color: red;
  font-weight: bold;
}

.signin-title {
  text-align: center;
  font-weight: bold;
  margin: 0;
}

.signup-option {
  background-color: rgb(69, 69, 185, 0.2);
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
}

.line {
  text-align: center;
  font-weight: bold;
  border-bottom: 2px solid rgb(245 239 239);
  line-height: 2px;
  margin: 25px 0;
}

.persontype-question{
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-size: 18px;
  margin-top: 8px;
  font-weight: bold;
}

.signin-fields {
  display: flex;
  flex-direction: column;
}

.signin-fields label {
  color: rgb(170 166 166);
  text-align: left;
  margin-top: 25px;
}

.signin-textbox {
  padding: 15px 20px;
  margin-top: 8px;
  margin-bottom: 15px;
  border: 1px solid #ccc;
  border-radius: 8px;
  box-sizing: border-box;
  outline: none;
}

.signin-button {
  background-color: rgb(69, 69, 185);
  color: white;
  padding: 18px 20px;
  margin-top: 25px;
  margin-bottom: 20px;
  margin-left: auto !important;
  margin-left: auto !important;
  width: 100%;
  border-radius: 10px;
  border: none;
  display: flex;
  align-items: center;
  justify-content: center;
}

.forget-pass {
  text-align: center;
  display: block;
}

.signup-question {
  text-align: center;
  font-weight: bold;
  margin-top: 15px !important;
  padding: 15px;
}

.dashboard {
  display: grid;
  place-items: center;
  height: 100vh;
  background-color: wheat;
  position: relative;
}

.dashboard-card {
  font-family: sans-serif;
  width: 80vw;
  height: 90vh;
  margin: 60px auto;
  display: flex;
  background-color: #fefaf5;
  border-radius: 15px;
  box-shadow: 0px 3px 5px -1px rgba(0, 0, 0, 0.2),
    0px 6px 10px 0px rgba(0, 0, 0, 0.14), 0px 1px 18px 0px rgba(0, 0, 0, 0.12);
}

/* Sidebar */

.dashboard-options {
  background-color: rgb(24, 33, 62);
  color: wheat;
  display: flex;
  flex-direction: column;
  flex: 0.25 1;
  width: 40vw;
  border-top-left-radius: 15px;
  border-bottom-left-radius: 15px;
}

.dashboard-logo {
  margin-top: 50px;
  margin-bottom: 50px !important;
  color: rgb(234, 68, 74);
  font-size: 30px;
  font-weight: 800;
  text-align: center;
}

.dashboard-option {
  text-decoration: none;
  padding: 15px;
  border-bottom: 0.5px solid salmon;
  border-top: 1px solid rgb(234, 68, 74, 0.5);
  color: #ffff;
  font-weight: 700;
  text-align: left;
  display: flex;
  align-items: center;
  cursor: pointer;
}

.dashboard-option:hover {
  color: rgb(234, 68, 74);
  border-left: 5px solid rgb(234, 68, 74);
  transition: all 0.5s ease;
  background-color: #ddd;
}

.dashboard-option.clicked {
  color: rgb(234, 68, 74);
  border-left: 5px solid rgb(234, 68, 74);
  transition: all 0.5s ease;
  background-color: #ddd;
}

/* Content of each option in Sidebar */

.dashboard-option-content {
  display: flex;
  flex: 0.75 1;
  width: 80vw;
}

.dashboard-option-icon {
  margin-left: 15px;
  margin-right: 15px;
}

.dashboard-addbooks-content,
.dashboard-addmember-content {
  display: flex;
  flex-direction: column;
  flex: 1 1;
  overflow-y: scroll;
}

.dashboard-addbooks-content::-webkit-scrollbar,
.dashboard-addmember-content::-webkit-scrollbar {
  display: none;
}

.dashboard-option-title {
  text-align: left;
  margin: 10px 25px !important;
  font-size: 20px;
  font-weight: 800;
}

.dashboard-title-line {
  text-align: center;
  font-weight: bold;
  border-bottom: 2px solid rgb(245 239 239);
  line-height: 2px;
  margin-bottom: 10px !important;
}

.shortinfo-table {
  margin-top: 5px !important;
  width: 100% !important;
}

@media screen and (min-width: 768px) {
  .sidebar-toggler {
    display: none !important;
  }
}

@media screen and (max-width: 768px) {
  .dashboard {
    margin-top: 60px;
    position: fixed;
  }
  .dashboard-card {
    margin-top: 0px;
    height: 100vh;
    width: 100vw;
    border-radius: 0 !important;
  }
  .sidebar-toggler {
    position: absolute;
    top: 0%;
    left: -2%;
    z-index: 100;
    height: 25px;
    width: 25px;
  }
  .dashboard-logo {
    font-size: 20px;
    margin-bottom: 50px !important;
  }

  /* Sidebar CSS */

  .dashboard-options {
    display: none;
  }
  .dashboard-options.active {
    position: relative;
    width: auto;
    min-width: 250px;
    height: 100vh;
    display: flex;
    position: fixed;
    left: 0;
    transition: all 0.5s ease;
    z-index: 5;
    border-top-left-radius: 0px;
    border-bottom-left-radius: 0px;
  }
  .dashboard-option-content {
    flex: 1 1 !important;
  }
}

