  display: flex;
  flex-direction: column;
  flex: 0.75 1;
  width: 80vw;
  overflow-y: scroll;
}

.member-profile-content,
.member-activebooks-content,
.member-reservedbooks-content,
.member-history-content {
  display: flex;
  flex-direction: column;
  flex: 1 1;
  margin: 25px 25px 100px 25px;
}

.dashboard-option-content::-webkit-scrollbar,
.member-profile-content::-webkit-scrollbar,
.member-activebooks-content::-webkit-scrollbar,
.member-reservedbooks-content::-webkit-scrollbar,
.member-history-content::-webkit-scrollbar {
  display: none;
}

.user-details-topbar {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  height: -moz-fit-content;
  height: fit-content;
  border-radius: 5px;
  border: 1px solid lightgray;
  padding: 15px 35px;
  box-shadow: 0px 3px 1px -2px rgba(0, 0, 0, 0.2),
    0px 2px 2px 0px rgba(0, 0, 0, 0.14), 0px 1px 5px 0px rgba(0, 0, 0, 0.12);
}

.user-profileimage {
  height: 150px;
  width: 150px;
  border-radius: 50%;
}

.user-name {
  font-size: 24px;
  font-weight: bold;
  line-height: 50px;
}

.user-id {
  font-size: 18px;
  color: rgb(161, 151, 0);
  font-weight: bold;
}

.user-email,
.user-phone {
  font-size: 18px;
  font-weight: bold;
  color: rgb(131, 131, 131);
}

.user-details-specific {
  display: flex;
}

.specific-left {
  display: flex;
  flex-direction: column;
  width: 50%;
  border: 1px solid lightgray;
  border-radius: 10px;
  padding: 25px;
  margin: 20px 10px 0 0;
  box-shadow: 0px 3px 1px -2px rgba(0, 0, 0, 0.2),
    0px 2px 2px 0px rgba(0, 0, 0, 0.14), 0px 1px 5px 0px rgba(0, 0, 0, 0.12);
}

.specific-left-top {
  display: flex;
  justify-content: space-between;
}

.specific-left-bottom {
  display: flex;
  justify-content: space-between;
  margin-top: 30px;
}

.specific-right {
  display: flex;
  flex-direction: column;
  width: 50%;
  padding: 15px;
  border: 1px solid lightgray;
  border-radius: 10px;
  margin: 20px 0 0 10px;
  box-shadow: 0px 3px 1px -2px rgba(0, 0, 0, 0.2),
    0px 2px 2px 0px rgba(0, 0, 0, 0.14), 0px 1px 5px 0px rgba(0, 0, 0, 0.12);
}

.specific-right-top,
.specific-right-bottom {
  display: flex;
  flex-direction: column;
  flex: 0.5 1;
}

.specific-left-topic {
  display: flex;
  flex: 0.5 1;
  flex-direction: column;
}

.specific-right-topic {
  font-size: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.member-dashboard-heading {
  margin: 22px 0;
  font-size: 22px;
  font-weight: bold;
}

.activebooks-table {
  font-family: Arial, Helvetica, sans-serif;
  border-collapse: collapse;
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  margin-top: 15px;
  margin-bottom: 10px !important;
  box-shadow: 0px 3px 1px -2px rgba(0, 0, 0, 0.2),
    0px 2px 2px 0px rgba(0, 0, 0, 0.14), 0px 1px 5px 0px rgba(0, 0, 0, 0.12);
}

.activebooks-table td,
.activebooks-table th {
  border: 1px solid #ddd;
  padding: 8px;
  font-size: 16px;
}

.activebooks-table th {
  padding-top: 12px;
  padding-bottom: 12px;
  text-align: left;
  background-color: rgb(24, 33, 62);
  color: white;
}

@media screen and (max-width: 768px) {
  .user-details-topbar {
    flex-direction: column;
  }
  .user-details-specific {
    flex-direction: column;
  }
  .specific-left,
  .specific-right {
    width: 100%;
    margin: 20px auto;
  }
}

.books-page{
    height: 100vh;
    width: 100vw;
    /* background-color: wheat; */
}

.books{
    margin: 60px auto;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    grid-gap: 20px;
    gap: 20px;
    padding: 15px;
    max-width: 1024px;
}

.book-card{
    display: flex;
    flex-direction: column;
    box-sizing: content-box;
    height: auto;
    width: 250px;
    align-items: center;
    padding: 25px;
    margin: 10px;
    box-shadow: 0px 2px 4px -1px rgba(0, 0, 0, 0.2), 0px 4px 5px 0px rgba(0, 0, 0, 0.14), 0px 1px 10px 0px rgba(0, 0, 0, 0.12);
    border-radius: 15px;
    position: relative;
    background-color: rgb(238, 238, 238);
}

.book-card img{
    width: 150px;
    height: auto;
    margin-bottom: 25px!important;
    border-radius: 18px;
}

.book-card img:hover{
    transform: scale(1.05);
    transition: all ease 1.3s;
}

.bookcard-title{
    word-wrap: break-word;
    text-align: center;
    font-weight: 600;
    font-size: 16px;
}

.bookcard-author{
    color: rgb(141, 140, 140);
    font-size: 14px;
    font-weight: 900;
}

.bookcard-category{
    display: flex;
    flex-wrap: wrap;
    position: absolute;
    bottom: 5%;
    left: 5%;
}

.bookcard-category p{
    padding: 8px 15px;
    background-color: lightgray;
    font-weight: 600;
    margin: 5px;
    border-radius: 5px;
}

.bookcard-emptybox{
    height: 50px;
    position: relative;
    bottom: 0%;
    background-color: rgb(0, 0, 0);
}
/*Desktop View*/
.header{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0px 40px;
    height: 60px;
    width: 100%;
    position: fixed;
    z-index: 100;
    background-color: rgb(16,25,13);
    box-shadow: 0 1px 4px rgba(146,161,176,.15);
    top: 0%;
}

.logo-nav a{
    font-size: 20px;
    color: rgb(255, 255, 255);
    font-weight: 800;
}

.logo-nav a:hover{
    color: wheat;
    transition: all ease 0.5s;
}

