.search-container {
    position: relative;
    justify-content: center;
    align-items: center;
    /* display: inline-block; */
    display: flex;
    max-width: 300px;
    width: 100%;
}

/* Search Input Field Styling */
.search-input {
    height: 35px;
    width: 500px;
    padding: 10px 40px 10px 15px;  /* Space for icon on right */
    font-size: 16px;
    border: 1px solid #ccc;
    border-radius: 25px;
    cursor: pointer;
    outline: none;
    transition: border-color 0.3s ease;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  }

  /* Hover and Focus Effects */
  .search-input:focus {
    border-color: #5cb85c;
    box-shadow: 0 4px 12px rgba(0, 128, 0, 0.2);
  }

  /* Clear Icon Styling */
  .clear-icon {
    position: absolute;
    top: 45%;
    right: 15px;
    transform: translateY(-50%);
    font-size: 28px;
    color: #aaa;
    cursor: pointer;
    transition: color 0.3s ease;
  }

  /* Clear Icon Hover Effect */
  .clear-icon:hover {
    color: #333;
  }
.nav-options{
    padding-left: 25px;
    display: flex;
    list-style-type: none;
    margin-bottom: 15px!important;
}

.nav-right{
    display: flex;
    align-items: center;
    justify-content: center;
}
  
.option{
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0px 25px;
    margin-top: 15px;
    color: rgb(243, 6, 6);
}

.option a{
    color: rgb(255, 255, 255);
    display: flex;
    align-items: center;
    font-weight: 800;
    font-size: 18px;
}

.option a:hover{
    color: rgb(214, 139, 0);
    transition: all ease 0.5s;
}

a{
    text-decoration: none!important;
}
  
.option:hover{
    color: rgb(6, 240, 170);
}

.mobile-menu{
    display: block;
}

/* Nav Options are given in this to make the search input as big as possible */

@media (min-width: 768px){
    .mobile-menu{
        display: none!important;
    }
    .header{
        display: grid;
        grid-template-columns: 10% 60%;
    }
}

@media (max-width: 768px){
    /*Mobile View */
    .header{
        padding: 0px 10px;
    }
    .nav-options{
        display: flex;
        width: 100%;
        height: 280px;
        position: absolute;
        top: 60px;
        left: -100%;
        opacity: 0;
        transition: all 0.5s ease;
        flex-direction: column;
        list-style-type: none;
        grid-gap: 0px;
    }
    .nav-options.active{
        background: black;
        left: 0;
        opacity: 1;
        transition: all 0.5s ease;
        z-index: 100;
        align-content: center;
        padding-left: 0px;
    }
  
    .menu-icon{
        width: 45px;
        height: 45px;
        color: rgb(233,83,75);
        padding: 2px;
        background-color: rgb(246, 233, 202);
        border-radius: 5px;
        cursor: pointer;
        margin:0px 5px 0px 10px;
    }
    .option{
        display: flex;
        align-items: center;
        justify-content: center;
        height: 10vw;
        padding: 30px 0px;
    }
}  
