/*Desktop View*/
.header{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0px 40px;
    height: 60px;
    width: 100%;
    position: fixed;
    z-index: 100;
    background-color: rgb(16,25,13);
    box-shadow: 0 1px 4px rgba(146,161,176,.15);
    top: 0%;
}

.logo-nav a{
    font-size: 20px;
    color: rgb(255, 255, 255);
    font-weight: 800;
}

.logo-nav a:hover{
    color: wheat;
    transition: all ease 0.5s;
}

